.stars {
  display: flex;
  gap: 4px;

  &__star {
    height: 16px;
    width: 16px;

    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;

    .stars--4 &:nth-child(-n + 4) {
      background-image: url(../images/star-active.svg);
    }
  }
}
